import coctel from '../images/coctel.jpg'; 
import harry from "../images/mago.jpg";
import number from "../images/guessnumber.jpg";
import awesome from "../images/card.PNG";
import juego from "../images/juego.jpg";
import simon from "../images/simon.png";


function getSrc(nameImage) {
  switch (nameImage) {
    case "coctel": {
      return coctel;
    }
    case "harry":
      return harry;

    case "number":
      return number;

    case "awesome":
      return awesome;

    case "juego":
      return juego;  

    case "simon":
      return simon; 

    
    default:
      break;
  }
}
export default getSrc;